import React, {Component} from 'react';
import {ajax} from '../../util/index';
import ip from '../../util/ipLocation';
let port = require('../../../mock/port');
import {Carousel,WhiteSpace,NavBar,Card,List,Radio,Stepper,Button,Flex} from 'antd-mobile';
const RadioItem=Radio.RadioItem;
export default class GoodsDetail extends Component {
    constructor() {
        super();
        this.state = {
            goods: {
                images:[],
                name: '',
                description: '',
                standard: [],
                avatar: ''
            },
            value:0,
            val:0
        }
    }

    componentWillMount() {
        ajax({
            url: `http://${ip}:${port}/detail/123`,
            method: 'GET'
        }).then((res) => {
            console.log(res.data);
            this.setState({goods:res.data})
        })
    }
    onChange = (value) => {
        this.setState({
            value,
        });
    };
    onChangeVal = (val) => {
        this.setState({
            val,
        });
    };
    format=(ary)=>{
        let aryData=[];
        ary.forEach((item,index)=>{
            aryData.push({value:index,label:item})
        })
        return aryData;
    }
    render() {
        let {value} = this.state;
        let {images,avatar,name,description,standard} = this.state.goods;
        return (
            <div>
                <NavBar mode="dark" className="head">
                    
                </NavBar>
                <Carousel
                    className="my-carousel"
                    autoplay={false}
                    infinite
                    selectedIndex={1}
                    swipeSpeed={35}
                >
                    {images.map(ii => (
                        <a href="#" key={ii} style={{height:200}}>
                            <img width="100%"
                                 src={ii}
                                 onLoad={() => {
                                     window.dispatchEvent(new Event('resize'));
                                     this.setState({
                                         initialHeight: null,
                                     });
                                 }}
                            />
                        </a>
                    ))}
                </Carousel>
                <WhiteSpace/>
                <Card full>
                    <Card.Header
                        title={name}
                        thumb={avatar}
                    />
                    <Card.Body>
                        <div>{description}</div>
                    </Card.Body>
                    <Card.Footer content="" extra={<div></div>} />
                </Card>
                <WhiteSpace/>
                <List renderHeader={() => ''}>
                    {this.format(standard).map(i => (
                        <RadioItem key={i.value} checked={value === i.value} onChange={() => this.onChange(i.value)}>
                            {i.label}
                        </RadioItem>
                    ))}
                </List>
                <WhiteSpace/>
                <list>
                    <List.Item
                        className="select"
                        wrap
                        extra={
                            <Stepper
                                style={{ width: '100%', minWidth: '2rem' }}
                                showNumber
                                max={10}
                                min={1}
                                value={this.state.val}
                                onChange={this.onChangeVal}
                                useTouch={false}
                            />
                        }
                    >
                        
                    </List.Item>
                </list>
                <Flex className="footer" justify="end">
                    <span className="car"></span>
                    <span className="buy"></span>
                </Flex>
            </div>
        )
    }
}
import './GoodsDetail.less'


